.container{
    display: block;
}
.about{
    display: flex;
    background-color:white;
    align-items: center;
    justify-content: center;
    padding: 50px 50px;
    column-gap: 25px;
}
.activities_img{
    width: 500px;
    height: 360px;
    border-radius: 50px;
    margin: 0px 0;
}
.description{
    width: 50%;
    
}
.description h1{
    color: black;
}
.description p{
    color: black;
}
.purpose{
    display: flex;
    justify-content: center;
    padding: 50px 50px;
    column-gap: 25px;
}


.doctors{
    display: flex;
    background-color:rgba(201, 201, 201, 0.205);
    flex-direction: column;
    padding: 0 50px 50px 50px;
    column-gap: 25px;
    overflow: hidden;
}
.doctors h1{
    margin-top: 25px;
    text-align: center;
    color: black;
}
.doctors p{
    text-align: center;
    color: black;
}

.doctors_cards{
    margin: 25px 0;
    display: flex;
    column-gap: 25px;
    row-gap: 25px;
}

.title{
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    padding: 0 10px;
}
.title h1{
    font-size: 3rem;
    color: rgba(0, 0, 0, 0.808);
}
.title p{
    font-size: 3rem;
    color: rgba(0, 0, 0, 0.575);
}
.accordeon_block{
    background-color: rgb(252, 214, 214);
    padding: 0 15px;
    border-radius: 25px;
    margin-bottom: 15px;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}
.check{
    width: 20px;
    height: 20px;
    position: relative;
    
}
.first_line{
    height: 3px;
    width: 12px;
    background-color: black;
    rotate: 35deg;
    position: absolute;
    top: 8px;
    transition:all .5s ease;
}
.first_line_active{
    height: 3px;
    width: 12px;
    background-color: black;
    rotate: 0;
    position: absolute;
    top: 8px;
    transition:all .5s ease;
}
.second_line{
    height: 3px;
    width: 12px;
    background-color: black;
    rotate: -35deg;
    position: absolute;
    top: 8px;
    right: 0;
    transition:all .5s ease;
}
.second_line_active{
    height: 3px;
    width: 12px;
    background-color: black;
    rotate: -0;
    position: absolute;
    top: 8px;
    right: 0;
    transition:all .5s ease;
}


.content{
    height: 0;
    transition:all .5s ease;
    overflow: hidden;
    opacity: 1;
}
.content_active{
    height: 240px;
    transition: all .5s ease;
    opacity: 1;
    overflow: hidden;
}

.doctor_card{
    position: relative;
    width: 350px;
    border-radius: 25px;
    background-color: white;
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    overflow: hidden;
}
.doctor_card img{
    width: 100%;
    border-radius: 25px 25px 0 0;
    transition: .3s all;
}
.doctor_card:hover img{
    width: 100%;
    border-radius: 25px 25px 0 0;
    scale: 1.1;
    transition: .3s all;
}
.doctor_card h2{
    color: black;
    font-size: 2rem;
}
.doctor_card p{
    color: black;
    padding-bottom: 10px;
    text-align: center;
}
.desc{
    position: absolute;
    bottom:85px;
    background-color: white;
    opacity: 0;
    transition: .3s all;
}
.doctor_card:hover {
    cursor: pointer;
} 
.doctor_card:hover .desc{
    opacity: 1;
    transition: .3s all;
} 


@media(max-width:915px){
    .container{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        column-gap: 50px;
        padding: 0 0px;
        overflow: hidden;
    }
    .about{
        display: flex;
        background-color:white;
        flex-direction: column-reverse;
        padding: 0 10px 25px 10px;
        column-gap: 25px;
    }
    .description{
        width: 100%;
        margin-top: 25px;
    }
    .activities_img{
        width: 90%;
        height: 40%;
        border-radius: 50px;
        margin: 25px auto;
    }
    .purpose{
        display: flex;
        justify-content: center;
        flex-direction: column;
        padding: 0 10px 25px 10px;
        column-gap: 25px;
    }
    .title h1{
        font-size: 30px;
        color: black;
    }
    .content{
        height: 0;
        transition:all .5s ease;
        overflow: hidden;
        opacity: 1;
    }
    .content_active{
        height: 430px;
        transition: all .5s ease;
        opacity: 1;
        overflow: hidden;
    }
 
    .doctors{
        display: flex;
        background-color:rgba(201, 201, 201, 0.205);
        flex-direction: column;
        padding: 0 10px 25px 10px;
        column-gap: 25px;
        width: 100%;
    }
    .doctors h1{
        text-align: left;
    }
    .doctors p{
        text-align: left;
    }
    
    .doctors_cards{
        margin: 25px 0;
        display: flex;
        row-gap: 25px;
    }
    .doctor_card{
        position: relative;
        width: 280px;
        border-radius: 25px;
        background-color: white;
        text-align: center;
        box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    }
}