.container{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    row-gap: 15px;
    column-gap: 5px;
}
.card{
    width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 10px;
}
.img{
    width: 140px;
    height: 140px;
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}
.name{
    text-align: center;
    color: rgb(39, 39, 39);
}
.desc{
    text-align: center;
    font-weight: 400;
    color: rgb(39, 39, 39);
}