.container{
    position: relative;
    padding: 0 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 25px 0;

}
h2{
    color: black;
    padding-top: 25px;
}
.title{
    display: flex;
    padding: 25px 0;
    column-gap: 25px;
}
.donate_page{
    display: flex;
    align-items: center;
    column-gap: 10px;
    padding: 15px 15px;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    margin-top: 50px;
    margin-bottom: 50px;
    border-radius: 15px;
    background-color:rgba(71, 174, 221, 0.205);
    transition: .3s all;
    z-index: 98;
    position: relative;
}
.donate_page:hover{
    
    transition: .3s all;
    z-index: 98;
    background-color: rgb(255, 219, 13);
}
.qr{
    width: 80px;
    height: 80px;
    background-color: white;
    border-radius: 10px;
    transition: .3s all;
    cursor: zoom-in;
    position: absolute;
    right: 15px;
}
.donate_page:has(.qr_active){
    background-color: rgb(255, 219, 13);
    color: rgb(255, 219, 13);
}
.qr_active{
    width: 200px;
    height: 200px;
    border-radius: 10px;
    background-color: white;
    transition: .3s all;
    cursor: zoom-out;
    position: absolute;
    right: 72px;
}





@media(max-width:915px){
    .container{
        position: relative;
        padding: 0 10px;
        display: flex;
        padding: 0 10px;
        text-align: center;
    
    }
    h2{
        color: black;
    }
    .donate_page{
        display: flex;
        width: 100%;
        padding: 15px 15px;
        box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
        margin-top: 0px;
        margin-bottom: 25px;
        border-radius: 15px;
        background-color:rgba(71, 174, 221, 0.205);
    }
    .title{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 25px 0;
        column-gap: 25px;
    }
    .title img{
        width:90%;
    }
    .qr{
        width: 10%;
        height: 10%;
        background-color: white;
        border-radius: 10px;
        transition: .3s all;
        cursor: zoom-in;
        position: absolute;
        right: 0px;
    }
    .qr_active{
        
        position: absolute;
        right: 100px;
    }
    
    .donate_page img{
        width: 70px;
        height: 70px;
        position: absolute;
        right: 15px;
    }
    .donate_page .qr_active{
        width: 135px;
        height: 135px;
        position: absolute;
        right: 35%;
    }

    
    
}


