.container{
    display: flex;
    /*background-color:rgba(71, 174, 221, 0.781);*/
    background-image: url(/public/images/image.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    align-items: center;
    height: 80vh;
    padding: 0 50px;
    justify-content: space-between;
    width: 100%;
    overflow: hidden;
    position: relative;
}
.title{
    width: 50%;
}
h1{
    font-size: 3rem;
    font-weight: bold;
    text-transform: uppercase;
    color: white;
}
p{
    color: white;
    margin: 15px 0;
    margin-bottom: 30px;
}
.read_more{
    display: inline-block;
    padding: 10px 15px;
    background-color: #ffffff;
    color: black;
    border-radius: 5px;
    transition: all 0.3s;
    border: 1px solid #ffffff;
}
.read_more:hover{
    display: inline-block;
    padding: 10px 15px;
    color: white;
    border-radius: 5px;
    transition: all 0.3s;
    border: 1px solid #ffffff;
    cursor: pointer;
    background-color:rgba(71, 174, 221, 0.781);
}
.img{
    border-radius: 0% 100% 0% 100% / 0% 0% 100% 100% ;
    position: absolute;
    right: -45%;
    top: 0px;
    width: 100%;
    height: 130%;
    background-image: url(/public/images/doctor.jpg);
    background-position: left;
    background-repeat: no-repeat;
    background-size: cover;

}
.donate_button{
    color: black;
    background-color: rgb(255, 219, 13);
    padding: 3px 20px 6px 20px;
    font-size: 20px;
    font-weight: 600;
    cursor: pointer;
    border-radius: 5px;
    background-image: url(./../images/heart.png);
    background-size: 20px;
    background-repeat: no-repeat;
    background-position: bottom 8px left -20px;
    filter: sepia(0);
}
.donate_button:hover{
    color: white;
    background-color: rgb(33, 41, 160);
    padding: 3px 20px 6px 80px;
    background-image: url(./../images/heart.png);
    background-size: 20px;
    background-repeat: no-repeat;
    background-position: bottom 8px left 20px;
    
}


@media(max-width:915px){
    .container{
        height: 90vh;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        column-gap: 50px;
        row-gap: 50px;
        padding: 0 10px;
    }
    .title{
        width: 100%;
        margin-top: 10vh;
    }
    .img{
        display: block;
        border-radius: 38% 62% 27% 73% / 60% 57% 43% 40% ;
        position: absolute;
        right: -40%;
        top: 58vh;
        width: 130%;
        height: 40%;
    
    }
}