.container{
    background-color:rgba(71, 174, 221, 0.781);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 25px 0;
}
.social{
    display: flex;
    column-gap: 15px;
    margin-bottom: 15px;
}
.icon{
    font-size: 30px;
    cursor: pointer;
    color: white;
    transition: .3s all;
}
.icon:hover{
    font-size: 30px;
    cursor: pointer;
    color: rgb(131, 45, 45);
    transition: .3s all;
}
a{
    text-decoration: none;
    margin-bottom: 15px;
    color: black;
    transition: .3s all;
}
a:hover{
    text-decoration: none;
    margin-bottom: 15px;
    color: white;
    transition: .3s all;
}