.container{
    display: flex;
    background-color:white;
    flex-direction: column;
    align-items: center;
    padding: 0 50px;
}
.container h1{
    color: black;
    margin-top: 20px;
}
.container p{
    color: black;
}
.cards{
    margin-top: 15px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: 25px;
    row-gap: 25px;
    margin-bottom: 25px;
}
.button_more{
    display: inline-block;
    padding: 10px 55px;
    background-color:rgba(71, 174, 221, 0.781);
    color: black;
    border-radius: 5px;
    transition: all 0.3s;
    border: 1px solid #ffffff;
    font-size: 1rem;
    margin-bottom: 50px;
}
.button_more:hover{
    display: inline-block;
    padding: 10px 55px;
    background-color:white;
    color: black;
    border-radius: 5px;
    transition: all 0.3s;
    border: 1px solid rgba(71, 174, 221, 0.781);
    font-size: 1rem;
    cursor: pointer;
}

.card{
    display: flex;
    column-gap: 15px;
    transition: all 0.3s; 
    background-color:rgba(71, 174, 221, 0.205);
    border-radius: 15px;
    padding: 15px;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    position: relative;
    overflow: hidden;
    cursor: default;
    
}
.card:hover{
    scale: 1.1;  
    transition: all 0.3s;
}
.card_img:hover{
    background-color: rgb(184, 170, 124);
    transition: all 0.3s;
}
.card .card_img{
    background-color:rgba(71, 174, 221, 0.781);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70px;
    height: 70px;
    border-radius: 50px;
    transition: all 0.3s;
}
.card .card_img img{
    width: 40px;
    height: 40px;
}
.card h1{
    margin-top: 25px;
    font-size: 1rem;
    color: black;
}



@media(max-width:915px){
    .container{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        column-gap: 50px;
        row-gap: 50px;
        padding: 0 10px;
    }
    .container h1{
        color: black;
        margin-top: 25px;
        margin-bottom: -50px;
    }
    .container p{
        color: black;
        margin-top: 20px;
        margin-bottom: -15px;
    }

    .cards{
        margin-top: 0;
        display: flex;
        column-gap: 25px;
        row-gap: 25px;
        justify-content: flex-start;
    }
    .card{
        width: 100%;
        display: flex;
        transition: all 0.3s; 
        background-color:rgba(71, 174, 221, 0.205);
        border-radius: 15px;
        padding: 15px;
    }
    .card:hover{
        scale: 1;     
    }
    
}
    




