.container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 20vh;
    font-size: 17px;
    padding: 0 50px;
    background-color:rgba(71, 174, 221, 0.781);
    padding-top: 13px;
    border-width: 0 0 10px 0;
    border-style: solid;
    border-color: rgb(255, 219, 13);
}

a.active{
    color: black;
    text-transform: uppercase;
    text-decoration: none;
    background-color: rgb(255, 219, 13);
    height: 100%;
    display: flex;
    justify-content: center;
    transition: .3 all;
    align-items: center;
}

.nav{
    display: flex;
    align-items: center;
    column-gap: 20px;
    height: 100%;

}
.link{
    text-transform: uppercase;
    text-decoration: none;
    color: white;
    position: relative;
    
    
}

.link:hover{
    color: white;
}
.logo img{
    width: 170px;
    height: 70px;
}
.burger{
    display: none;
}
.donate_button{
    color: black;
    background-color: rgb(255, 219, 13);
    padding: 3px 20px 6px 20px;
    font-size: 20px;
    font-weight: 600;
    cursor: pointer;
    border-radius: 5px;
}
.donate_button:hover{
    color: white;
    background-color: rgb(33, 41, 160);
}
.language{
    list-style: none;
    display: flex;
    height: 10vh;
    padding-top: 14px;
}
.language li button{
    background-color: rgba(28,28,28,0);
    border: none;
    margin-left: 5px;
    font-size: 16px;
    cursor: pointer;
}

@media(max-width:915px){
    .container{
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 10vh;
        font-size: 20px;
        padding: 0 10px;
        background-color:rgba(71, 174, 221, 0.781);
        z-index: 99;
        padding-top: 0;
    }
    .logo img{
        width: 90px;
        height: 40px;
    }
    .nav{
        display: flex;
        flex-direction: column;
        text-align: center;
        justify-content: center;
        height: 90vh;
        width: 100%;
        row-gap: 20px;
        font-size: 30px;
        position: absolute;
        top:10vh;
        left: -100%;
        background-color: rgb(245, 245, 220);
        transition: .5s all ease;
        z-index: 99;

    }
    .nav_active{
        display: flex;
        flex-direction: column;
        text-align: center;
        justify-content: center;
        height: 90vh;
        width: 100%;
        row-gap: 20px;
        font-size: 30px;
        position: absolute;
        top:10vh;
        left: 0;
        background-color:rgb(71, 174, 221);
        transition: .5s all ease;
        z-index: 99;
    }
    .burger{
        width: 30px;
        height: 30px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        cursor: pointer;
        overflow: hidden;
        
    }
    .line1{
        width: 30px;
        height: 3px;
        background-color: rgb(0, 0, 0);
        transition: .5s all ease;
    }
    .line1_active{
        width: 30px;
        height: 3px;
        background-color: rgb(0, 0, 0);
        transition: .5s all ease;
        margin-top: 13px;
        rotate: -140deg;
        
        
    }
    .line2{
        width: 30px;
        height: 3px;
        background-color: rgb(0, 0, 0);
        transition: .5s all ease;
        margin: 0 auto;
    }
    .line2_active{
        width: 30px;
        height: 3px;
        background-color: rgb(0, 0, 0);
        transition: .6s all ease;
        rotate: 140deg;
        margin-bottom: 15px;
    }
    .line3{
        width: 30px;
        height: 3px;
        background-color: rgb(0, 0, 0);
        transition: .6s all ease;
    }
    .line3_active{
        width: 30px;
        height: 3px;
        background-color:rgb(71, 174, 221);
        transition: .6s all ease;
        margin-bottom: -5px;
        
    }
    .language li button{
        background-color: rgba(28,28,28,0);
        border: none;
        margin-left: 5px;
        font-size: 30px;
        cursor: pointer;
    }
    a.active{
        color: white;
        text-transform: uppercase;
        text-decoration: none;
        background-color: rgb(255, 219, 13);
        height: 5%;
        display: flex;
        justify-content: center;
        transition: .3 all;
        align-items: center;
    }
   
}